.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App {
  display: flex;
}
.main,
.features {
  flex: 1;
}
.main {
  margin-left: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sortable-item {
  padding: 10px;
  padding-left: 0;
  user-select: none;
}

.completed-todo {
  padding: 10px;
}
.handle {
  padding: 0 10px;
  cursor: grab;
}

.delete-btn {
  margin-left: 10px;
}
.todo-text {
  margin-left: 10px;
}
button {
  cursor: pointer;
}
